const {
  findAll,
  findByUrl,
  createRecord,
  insuranceEmail,
  findByCountry,
  fetchByUuid
} = require("./index.js");
const router = require("express").Router();

router.route("/").get(findAll).post(createRecord);

router.route("/insurance").post(insuranceEmail);

router.route('/fetch/expert/by/selected/country')
    .post(findByCountry);

router
  .route("/:url")
  // .put(editMeta)
  // .delete(removeMeta)
  .get(findByUrl);

router
  .route("/query/:id")
  .get(fetchByUuid);

// router.route('/edit/:id')
//     .put(editMeta);

module.exports = router;
